.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;

  @include hover(transform, scale(1.1));

  @include onTablet {
    height: 32px;
    width: 32px;
  }

  &--phone {
    background-image: url(/images/icons/icon-phone-2x.png);
    text-decoration: none;

    &:hover::before {
      content: "+1 234 5555-55-55";
      display: block;
      width: max-content;
      transform: translate(-100%, 50%);
      font-weight: bold;
      font-size: 12px;
      line-height: 16px;
      color: $main-text-color;
    }
  }

  &--menu {
    background-image: url(/images/icons/Icon-burger-menu-2x.png);

    &:hover {
      background-image: url(/images/icons/Icon-burger-menu-hover-2x.png);
    }
  }

  &--close {
    background-image: url(/images/icons/icon-close-2x.png);
  }
}
